
import Vue from 'vue';

export default Vue.extend({
  name: 'HelloWorld',
  props: {
    msg: String
  },
  computed: {
    count(): number {
      return this.$store.state.count
    },
  },
  methods: {
    increment() {
      this.$store.commit('increment')
    },
    initCount() {
      this.$store.dispatch("resetCount")
    },
  },
});
